import { Component, Inject } from '@angular/core';
import { FormControl, UntypedFormGroup, Validators, AbstractControl } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LocalStorageService } from '../../services/local-storage.service';
import { VesselData } from '../../data-models/user-models/vessel-data.model';
import { UserData } from '../../data-models/user-models/user-data.model';
import { Gear } from '../../data-models/trip-models/gear-models/gear.model';

@Component({
  selector: 'app-add-logbook-dialog',
  templateUrl: './add-logbook-dialog.component.html',
  styleUrls: ['./add-logbook-dialog.component.css']
})
export class AddLogbookDialogComponent {

  userDetails: UserData = new UserData('', '');
  gillNetFlag: boolean = false;

  logbookForm: UntypedFormGroup = new UntypedFormGroup({
    logbookNumber: new FormControl<string>('', [Validators.required]),
    gearType: new FormControl<string>('', [Validators.required]),
    vesselOption: new FormControl<string>(''), // conditional validation
    vesselName: new FormControl<string>(''), // conditional validation
    vesselNumber: new FormControl<string>(''), // conditional validation
    vessel: new FormControl<VesselData>(new VesselData('', '')) // conditional validation
  });

  constructor (
    public dialogRef: MatDialogRef<AddLogbookDialogComponent>,
    private localStorageService: LocalStorageService,
    @Inject(MAT_DIALOG_DATA) public data: { userDetails: UserData }
  ) {

    // determine if the user has vessels
    this.userDetails = this.data.userDetails;
    if (!this.userDetails.vessels.length) { 
      // the user does not have pre-existing vessels
      this.addValidation('vesselName');
      this.addValidation('vesselNumber');
    } else {
      // the user has pre-existing vessels
      this.addValidation('vesselOption');
    }
  }

  addValidation(formName: string) {
    const control: AbstractControl<any, any> | null = this.logbookForm.get(formName);
    control?.setValidators([Validators.required]);
    control?.updateValueAndValidity();
  }

  removeValidation(formName: string) {
    const control: AbstractControl<any, any> | null = this.logbookForm.get(formName);
    control?.clearValidators();
    control?.updateValueAndValidity();
  }

  onVesselOptionChange(): void {
    this.logbookForm.get('vesselOption')?.valueChanges.subscribe((value: string) => {
      if (value === 'Add New Vessel') {
        this.addValidation('vesselName');
        this.addValidation('vesselNumber');
        this.removeValidation('vessel');
      } else {
        this.removeValidation('vesselName');
        this.removeValidation('vesselNumber');
        console.log('adding validation to select vessel');
        this.addValidation('vessel');
      }
    });
  }

  ngOnInit(): void {
    this.onVesselOptionChange();
  }

  saveData(): void {
    let vessel: VesselData = new VesselData('', ''); 
    // the user is adding a new vessel
    if (this.logbookForm.get('vesselOption')?.value === 'Add New Vessel' || !this.userDetails.vessels.length) {
      // we need to add a new vessel to the user's list
      vessel.vesselName = this.logbookForm.get('vesselName')?.value;
      vessel.vesselRegistrationNumber = this.logbookForm.get('vesselNumber')?.value;
      this.userDetails.vessels.push(vessel); // add the vessel to the list of user vessels
    } else {
      // the user has chosen a pre-existing vessel
      vessel = this.logbookForm.get('vessel')?.value;
    }
    let newLogbook: Gear = new Gear();
    newLogbook.name = this.logbookForm.get('gearType')?.value;
    this.gillNetFlag = (newLogbook.name === 'Gill Net'); // set flag
    newLogbook.logbookID = this.logbookForm.get('logbookNumber')?.value;
    newLogbook.vessel = vessel;
    this.userDetails.gears.push(newLogbook);

    LocalStorageService.saveUser(this.userDetails).then(() => {
      console.log("User data saved to Pouch");
    });    
  }

  closeDialog(): void {
    this.dialogRef.close('Back');
  }

  addLogbook() { 
    if (this.logbookForm.valid) {
      this.saveData();
      if (this.gillNetFlag) {
        this.dialogRef.close('Gill Net');
      } else {
        this.dialogRef.close('add');
      }
      
    } else {
      this.logbookForm.markAllAsTouched();
    }    
  }


}
