import { Component } from '@angular/core';
import { AuthService, User } from '@auth0/auth0-angular';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { Gear } from '../../data-models/trip-models/gear-models/gear.model';
import { UserData } from '../../data-models/user-models/user-data.model';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';

@Component({
  selector: 'app-resources',
  templateUrl: './resources.component.html',
  styleUrls: ['./resources.component.css']
})
export class ResourcesComponent {

  userDetails: UserData = new UserData('', '');

  speciesGuides: string[][] = [];
  gillNetMaps: string[][] = [];
  seineMaps: string[][] = [];
  trollMaps: string[][] = [];
  isGuide: boolean = false;
  isMap: boolean = false;
  path: string = '../../../assets/pdfs/';
  filename: string = '';
  logbooks: Gear[] = [];

  constructor(
    private auth: AuthService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      if (currUsername) {
        LocalStorageService.getUserOffline(currUsername).then(() => {
          let error: string = LocalStorageService.error;
          if (error) {
            this.authenticationService.userDataLogout(error);
          }
          let currUser: UserData = LocalStorageService.currUser;
          if (!currUser || !currUser.userID || !currUser.userEmail) {
            this.authenticationService.userDataLogout("User ID or Email not defined");
          }
          else {
            this.userDetails = currUser;
            this.initialize();
          }
        });
      }
      else {
        this.authenticationService.userDataLogout("Username not saved to storage");
      }
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUserOnline(id, email).then(() => {
                  let error: string = LocalStorageService.error;
                  if (error) {
                    this.authenticationService.userDataLogout(error);
                  }
                  let currUser: UserData = LocalStorageService.currUser;
                  if (!currUser || !currUser.userID || !currUser.userEmail) {
                    this.authenticationService.userDataLogout("User ID or Email is undefined");
                  }
                  else {
                    this.userDetails = currUser;
                    this.initialize();
                  }
                });
              }
              else {
                this.authenticationService.userDataLogout("User ID or email is undefined");
              }
            }
            else {
              this.authenticationService.userDataLogout("Cannot get user data");
            }
          });
        }
        else {
          this.authenticationService.userDataLogout("Not authenticated");
        }
      });
    }

  }

  initialize(): void {

    // define species guide pdfs
    this.speciesGuides = [
      ['Pacific Coastal Birds', 'jPacific_Coastal_Birds_2012.pdf'],
      ['Sturgeon ID Sheet', 'lSturgeon ID Sheet_RG.pdf'],
      ['Common Ground Fish', 'icommongroundfish_poster.pdf'],
      ['Marine Mammal Guide', 'kBCCSN ID Guide_2010 revised.pdf']
    ];

    // determine what maps are available to the user based on their available logbooks
   this.logbooks = this.userDetails.gears;
    for (let gear of this.logbooks) {
      if (gear.name === 'Gill Net') {
        this.gillNetMaps.push(['License Areas (C-E)', 'ggearareamapCDEforGillnet.PDF']);
        this.gillNetMaps.push(['Subareas For Nets', 'hLogbook Subareas For Nets.pdf']);
      }
      else if (gear.name === 'Seine') {
        this.seineMaps.push(['License Areas (A-B)', 'ggearareamapsprintABforSeine.pdf']);
        this.seineMaps.push(['Subareas For Nets', 'hLogbook Subareas For Nets.pdf']);
      }
      else if (gear.name === 'Troll') {
        this.trollMaps.push(['License Areas (F-H)', 'lgearareamapsF-H.pdf']);
        this.trollMaps.push(['Zones and Subareas For Troll', 'mLogbook Zones and Subareas For Troll - colour.pdf']);
      }
    }

  }

  // which resource option the user clicked
  resourceClick(type: number, index: number): void {

    // species identification guide
    if (type === 0) {
      this.isGuide = true;
      this.filename = this.speciesGuides[index][1];
    }

    // maps
    else if (type === 1) {
      this.isMap = true;
      this.filename = this.gillNetMaps[index][1];
    }
    else if (type === 2) {
      this.isMap = true;
      this.filename = this.seineMaps[index][1];
    }
    else if (type === 3) {
      this.isMap = true;
      this.filename = this.trollMaps[index][1];
    }

  }

  // back to resources list
  backToResources(): void {
    this.isGuide = false;
    this.isMap = false;
    this.filename = '';
  }

}
