import { VesselData } from "../../user-models/vessel-data.model";

export class Gear {

  name: string = '';

  logbookID: string = '';

  vessel: VesselData = new VesselData('', ''); // associated vessel

  isChecked: boolean = false;

  constructor(name: string = '', id: string = '', vessel: VesselData = new VesselData('', ''), checked: boolean = false) {
    this.name = name;
    this.logbookID = id;
    this.vessel = vessel;
    this.isChecked = checked;
  }

  public toString(): string {
    return this.name + ' (Logbook #' + this.logbookID + ')';
  }

  public isValid(): boolean {
    return !!this.name && !!this.logbookID;
  }

  public isEqual(inputGear: Gear): boolean {
     return inputGear.logbookID === this.logbookID;
  }

  // transform anonymous data to gear object
  public static transformAnonymous(gear: any): Gear {
    return new Gear(
      gear.name ?? '',
      gear.logbookID ?? '',
      VesselData.transformAnonymous(gear.vessel),
      gear.isChecked ?? false
    );
  }

}
