import { Component, ViewChild } from '@angular/core';
import { FormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService, User } from '@auth0/auth0-angular';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { Trip } from '../../data-models/trip-models/trip.model';
import { UserData } from '../../data-models/user-models/user-data.model';
import { Gear } from '../../data-models/trip-models/gear-models/gear.model';
import { VesselData } from '../../data-models/user-models/vessel-data.model';
import { LicenseArea } from '../../data-models/trip-models/license-area.model';
import { Tel, TelInputComponent } from '../../tel-input/tel-input.component';
import { generalValidator } from '../../data-models/validator.model';

@Component({
  selector: 'app-start-report',
  templateUrl: './start-report.component.html',
  styleUrls: ['./start-report.component.css']
})
export class StartReportComponent {

  @ViewChild('telInput') telInput!: TelInputComponent;

  // data preambles
  currTrip: Trip = new Trip();
  userDetails: UserData = new UserData('', '');
  gears: Gear[] = [];
  userVessels: VesselData[] = [];
  licenseAreas: LicenseArea[] = [];
  emptyGear: Gear = new Gear('', '');
  emptyVessel: VesselData = new VesselData('', '');
  emptyArea: LicenseArea = new LicenseArea('', '');
  prevTrips: Trip[] = [];

  selectedGear: Gear = new Gear('', ''); // current selected logbook/gear
  
  // report data
  startForm: UntypedFormGroup = new UntypedFormGroup({
    gear: new FormControl<Gear>(this.emptyGear, [Validators.required]), // page 1
    licenseArea: new FormControl<LicenseArea>(this.emptyArea, [Validators.required]), // page 2
    master: new FormControl<string>('', [Validators.required]), // page 3
    fin: new FormControl<string>('', [Validators.required]), // page 3
    managementArea: new FormControl<string>('', [Validators.required]), // page 4
    name: new FormControl<string>('', [Validators.required]), // page 5
    phoneNum: new FormControl<Tel>(new Tel('', '', ''), [Validators.required, generalValidator()]), // page 5
    comments: new FormControl<string>('') // page 6
  });
  startDate: Date = new Date(); // page 4
  startTime: { hour: number, minute: number } = { hour: 0, minute: 0 }; // page 4

  // page to page
  numPages: number = 0;
  currPage: number = 0;
  spinnerProgress: number = 0;
  spinnerIncrement: number = 0;


  constructor(
    private auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      if (currUsername) {
        LocalStorageService.getUserOffline(currUsername).then(() => {
          let error: string = LocalStorageService.error;
          if (error) {
            this.authenticationService.userDataLogout(error);
          }
          let currUser: UserData = LocalStorageService.currUser;
          if (!currUser || !currUser.userID || !currUser.userEmail) {
            this.authenticationService.userDataLogout("User ID or Email not defined");
          }
          else {
            this.userDetails = currUser;
            this.initialize();
          }
        });
      }
      else {
        this.authenticationService.userDataLogout("Username not saved to storage");
      }
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUserOnline(id, email).then(() => {
                  let error: string = LocalStorageService.error;
                  if (error) {
                    this.authenticationService.userDataLogout(error);
                  }
                  let currUser: UserData = LocalStorageService.currUser;
                  if (!currUser || !currUser.userID || !currUser.userEmail) {
                    this.authenticationService.userDataLogout("User ID or Email is undefined");
                  }
                  else {
                    this.userDetails = currUser;
                    this.initialize();
                  }
                });
              }
              else {
                this.authenticationService.userDataLogout("User ID or email is undefined");
              }
            }
            else {
              this.authenticationService.userDataLogout("Cannot get user data");
            }
          });
        }
        else {
          this.authenticationService.userDataLogout("Not authenticated");
        }
      });
    }

  }

  // initialize the data
  initialize(): void {

    // get all gears, vessels, license areas
    this.gears = this.userDetails.gears;
    this.userVessels = this.userDetails.vessels;
    this.licenseAreas = LocalStorageService.getLicenseAreas();

     // set the start time to the current time
     this.startTime = {
      hour: this.startDate.getHours(),
      minute: this.startDate.getMinutes()
    };

    // initialize form 
    let currGear: Gear = this.emptyGear;
    let currArea: LicenseArea = this.emptyArea;

    let currMaster: string = '';
    let currFin: string = '';
    let currManagementArea: string = '';
    let currName: string = '';
    let currPhoneNum: Tel = new Tel('', '', '');
    let currComments: string = '';

    if (this.userDetails.prevTrips.length > 0) { // populate form data with data from the previous trip

      let tempTrip: Trip = new Trip();

      tempTrip = this.userDetails.prevTrips[this.userDetails.prevTrips.length-1];
      currMaster = tempTrip.master;
      currFin = tempTrip.fin;
      currManagementArea = tempTrip.startFishingReport.managementArea;
      currName = tempTrip.contactName;
      currPhoneNum = tempTrip.phoneNum;
      currComments = tempTrip.startFishingReport.comments;

      currGear = this.setSelectedGear(new Gear('', ''), tempTrip.gear);
      currArea = this.setSelectedArea(new LicenseArea('', ''), tempTrip.licenseArea);

    } else { // populate some form data 
      
      // fill in contact details based on info provided at sign-up
      currName = this.userDetails.firstName + ' ' + this.userDetails.lastName;
      currPhoneNum = this.userDetails.phoneNum;
      currGear = this.gears?.length ? this.gears[0] : this.emptyGear; 
      currGear.isChecked = true;

      let licenseIndex: number = 0;
      if (currGear.name === 'Gill Net') {
        licenseIndex = 2;
      } else if (currGear.name === 'Troll') {
        licenseIndex = 5;
      }
      currArea = this.setSelectedArea(new LicenseArea('', ''), this.licenseAreas[licenseIndex]);
      this.startForm.controls['licenseArea'].setValue(currArea);

    }

    // populate report
    this.startForm.patchValue({
      gear: currGear,
      licenseArea: currArea,
      master: currMaster,
      fin: currFin,
      managementArea: currManagementArea,
      name: currName,
      phoneNum: currPhoneNum,
      comments: currComments
    });

    this.numPages = 6;
    this.spinnerIncrement = 100 / this.numPages;
    this.toNextPage();

  }

  ngOnInit(): void {
    this.onGearChange();
  }

  onGearChange(): void {

    // if we change gear types, set the license area to the first of each list
    this.startForm.get('gear')?.valueChanges.subscribe((value: Gear) => {
      let licenseIndex: number = 0;
      if (!(this.selectedGear.name === value.name && this.selectedGear.logbookID === value.logbookID)) {
        if (value.name === 'Gill Net') {
          licenseIndex = 2;
        } else if (value.name === 'Troll') {
          licenseIndex = 5;
        }
        this.startForm.controls['licenseArea'].setValue(this.setSelectedArea(new LicenseArea('', ''), this.licenseAreas[licenseIndex]));
      }
    });
  }

  // set the radio buttons to the currently selected gear in the fishing report
  setSelectedGear(currGear: Gear, formGear: Gear): Gear {
    currGear = formGear;
    for (let gear of this.gears) {
      gear.isChecked = (gear.logbookID === currGear.logbookID);
    }
    return currGear;
  }

  // set the radio buttons to the currently selected vessel in the fishing report
  setSelectedArea(currArea: LicenseArea, formArea: LicenseArea): LicenseArea {
    currArea = formArea;
    for (let licenseArea of this.licenseAreas) {
      licenseArea.isChecked = (licenseArea.licenseName === currArea.licenseName);
    }
    return currArea;
  }

  // check current page before going to the next one
  toNextPage(): void {

    if (this.currPage <= this.numPages) {

      switch (this.currPage) {

        // to license area page
        case 1:
          let currArea: LicenseArea = new LicenseArea('', '');
          currArea = this.setSelectedArea(currArea, LicenseArea.transformAnonymous(this.startForm.get('licenseArea')?.value));
          this.startForm.patchValue({
            licenseArea: currArea
          });
          break;

        case 2:
          this.selectedGear = Gear.transformAnonymous(this.startForm.get('gear')?.value);
          break;

        // save date in date page
        case 4:
          this.setDateTime();
          break;

        // save phone number in contact details page
        case 5:
          this.telInput.touched = true;
          this.telInput.stateChanges.next();
          this.startForm.get('phoneNum')?.updateValueAndValidity();
          break;

        default:
          break;

      }

      this.incrementPage();
    }

    else {
      this.sendToDFO();
    }

  }

  // forward one page
  incrementPage(): void {
    this.currPage++;
    this.spinnerProgress += this.spinnerIncrement;
  }

  // back one page
  toPreviousPage(): void {

    switch (this.currPage) {

      // back to home if at beginning of report
      case 1:
        this.toHome();
        break;

       // to gear page
      case 2:
        let gearVal = Gear.transformAnonymous(this.startForm.get('gear')?.value);
        this.selectedGear = gearVal;
        let currGear: Gear = new Gear('', '');
        currGear = this.setSelectedGear(currGear, gearVal);
        this.startForm.patchValue({
          gear: currGear
        });
        break;
      
      // to license area page
      case 3:
        let areaVal = LicenseArea.transformAnonymous(this.startForm.get('licenseArea')?.value);
        let currArea: LicenseArea = new LicenseArea('', '');
        currArea = this.setSelectedArea(currArea, areaVal);
        this.startForm.patchValue({
          licenseArea: currArea
        });
        break;

    }

    this.currPage--;
    this.spinnerProgress -= this.spinnerIncrement;

  }

  // save the current trip & Start Fishing Report information
  saveCurrTrip(): void {
    this.currTrip.gear = Gear.transformAnonymous(this.startForm.get('gear')?.value);
    this.currTrip.vessel = this.currTrip.gear.vessel;
    this.currTrip.licenseArea = LicenseArea.transformAnonymous(this.startForm.get('licenseArea')?.value);
    this.currTrip.master = this.startForm.get('master')?.value;
    this.currTrip.fin = this.startForm.get('fin')?.value;
    this.currTrip.startFishingReport.startDate = this.startDate;
    this.currTrip.startFishingReport.managementArea = this.startForm.get('managementArea')?.value;
    this.currTrip.contactName = this.startForm.get('name')?.value;
    this.currTrip.phoneNum = this.startForm.get('phoneNum')?.value;
    this.currTrip.startFishingReport.comments = this.startForm.get('comments')?.value;
    this.currTrip.startFishingReport.submissionDate = new Date(); // get the current date
    this.userDetails.currTrip = this.currTrip;
  }

  // sets flag in trip data if this trip may need an offload report
  determineOffloadReport(): void {
    // we only need an offload report if the trip is Seine in Area B, or Gill Net in Area C, D, or, E
    let licenseName: string = this.currTrip.licenseArea.licenseName;
    let gearName: string = this.currTrip.gear.name;
    let isSeineOffload: boolean = gearName === 'Seine' && licenseName === 'B';
    let isGillOffload: boolean = gearName === 'Gill Net' && (licenseName === 'C' || licenseName === 'D' || licenseName === 'E');
    this.currTrip.offloadNeeded = isSeineOffload || isGillOffload;
    this.userDetails.currTrip = this.currTrip; // save flag changes
  }
  

  // update the start date hour and minute, only needed on 'Next'
  setDateTime(): void {

    let startHours: number = this.startTime.hour;
    let startMinutes: number = this.startTime.minute;

    this.startDate.setHours(startHours ? startHours : this.startDate.getHours());
    this.startDate.setMinutes(startMinutes ? startMinutes : this.startDate.getMinutes());
    this.startDate.setSeconds(0);
    this.startDate.setMilliseconds(0);

  }

  // check if gear value is invalid
  gearInvalid(): boolean {
    let gear: Gear = Gear.transformAnonymous(this.startForm.get('gear')?.value);
    return !gear || !gear.isValid();
  }

  // check if master values are invalid
  masterInvalid(): boolean {
    return this.startForm.get('master')?.errors?.required || this.startForm.get('fin')?.errors?.required;
  }

  // check if vessel is invalid
  vesselInvalid(): boolean {
    let vessel: VesselData = VesselData.transformAnonymous(this.startForm.get('gear')?.value.vessel);
    return !vessel || !vessel.isValid();
  }

  // check if license area is invalid
  licenseAreaInvalid(): boolean {
    let licenseArea: LicenseArea = LicenseArea.transformAnonymous(this.startForm.get('licenseArea')?.value);
    return !licenseArea || !licenseArea.isValid();
  }

  // check if management area is invalid
  managementAreaInvalid(): boolean {
    return this.startForm.get('managementArea')?.errors?.required;
  }

  // check if contact details are invalid
  contactDetailsInvalid(): boolean {
    return this.startForm.get('name')?.errors?.required || this.startForm.get('phoneNum')?.errors?.required;
  }

  determineAreaClass(area: LicenseArea): string {
    if (area.gearType !== this.startForm.get('gear')?.value.name) {
      return "hide";
    }
      return "";
  }

  // send start fishing report to DFO
  sendToDFO(): void {

    if (this.startForm.valid && !this.gearInvalid() && !this.vesselInvalid() && !this.licenseAreaInvalid()) {
      this.currTrip.startFishingReport.isInProgress = false;
      this.currTrip.startFishingReport.isComplete = true;
      this.saveCurrTrip(); // update the user's active trip
      this.determineOffloadReport(); // check if we will need an offload report for this trip
      this.updateHomepage(); // update the user's active homepage
      LocalStorageService.saveUser(this.userDetails).then(() => {
        this.router.navigate(['submitted-report'], { state: { name: 'Trip' } });
      });
    }
    else {
      this.toastr.error("Not all required fields filled", "Invalid form");
    }

  }

  updateHomepage(): void {
    this.userDetails.currHomepage = 'active-trip-home';
  }

  // go back to the home screen with no active trip
  toHome(): void {
    this.router.navigate(['no-trip-home']);
  }

}
