export class Net {

  netType: string = ''; // will be either Alaska Twist, Multi Strand, or Comibination

  netName: string = ''; // will only be for the skipper's reference

  length: string = '';

  weedlineDepth: string = '';

  weedlineDepthUnits: string = '';

  hangRatio: string = '';

  meshSize: string = '';

  meshSizeUnits: string = '';

  numMeshes: string = '';

  numStrands: string = ''; //only needed for Alaska Twist

  isChecked: boolean = false;

  constructor(type: string, name: string, length: string,
    depth: string, depthUnits: string, ratio: string, mesh: string,
    meshUnits: string, numMeshes: string, strands: string = '', checked: boolean = false) {

    this.netType = type;
    this.netName = name;
    this.length = length;
    this.weedlineDepth = depth;
    this.weedlineDepthUnits = depthUnits;
    this.hangRatio = ratio;
    this.meshSize = mesh;
    this.meshSizeUnits = meshUnits;
    this.numMeshes = numMeshes;
    this.numStrands = strands;
    this.isChecked = checked;
  }

  public toString(): string {
    return this.netName + ' (' + this.netType + ')';
  }

  public isValid(): boolean {

    let baseValid: boolean = !(!this.netName || !this.length || !this.weedlineDepth || !this.weedlineDepthUnits ||
      !this.hangRatio || !this.meshSize || !this.meshSizeUnits || !this.numMeshes);

    if (this.netType === "Alaska Twist") { // requires numStrands to be set
      return baseValid && !!this.numStrands;
    }
    else if (this.netType) { // does not require a value for numStrands
      return baseValid;
    }

    return false;

  }

  // transform anonymous data into net object
  public static transformAnonymous(net: any): Net {
    return new Net(
      net.netType ?? '',
      net.netName ?? '',
      net.length ?? '',
      net.weedlineDepth ?? '',
      net.weedlineDepthUnits ?? '',
      net.hangRatio ?? '',
      net.meshSize ?? '',
      net.meshSizeUnits ?? '',
      net.numMeshes ?? '',
      net.numStrands ?? '',
      net.isChecked ?? false
    );
  }
    
}
